<template>
  <div class="pop-up">
    <img class="close-icon" src="../../assets/icons/closeIcon.png" @click="$emit('close-login')"/>
    <div class="pop-up-title">登陆云思管理系统</div>
    <div class="input-container">
      <div class="input-title">手机号：</div>
      <el-input class="phone-num-input" v-model="phoneNum" placeholder="请输入手机号"></el-input>
    </div>
    <div class="input-container">
      <div class="input-title">输入密码：</div>
      <el-input class="phone-num-input" v-model="password" show-password></el-input>
    </div>
    <div class="input-container">
      <el-button class="login-button" type="primary" @click="goLogin">登陆</el-button>
    </div>
  </div>
</template>

<script>
const WEBAPI = require('apifm-webapi')
export default {
  data() {
    return {
      phoneNum: '',
      password: ''
    }
  },
  methods: {
    goLogin: function () {
      WEBAPI.login_mobile(this.phoneNum, this.password, ' ', ' ').then((res) => {
        if (res.code === 0) {
          this.$emit('close-login')
          this.$store.commit('SET_LOGIN_STATE', true)
        } else {
          this.$store.commit('SET_LOGIN_STATE', false)
        }
      })
    }
  }
}
</script>

