<template>
  <div id="app">
    <Alert v-if="showAlert"/>
    <NavigationBar 
      @change-tab="changeTab"
      @show-login-popup="showLoginPopup"
      @show-register-popup="showRegisterPopup"
      :style="{'position': navBarPosition}"
    />
    <router-view></router-view>
    <RegisterPopup v-if="showRegister" @close-register="closeRegisterPopup"/>
    <LoginPopup v-if="showLogin" @close-login="closeLoginPopup"/>
    <Footer />
  </div>
</template>

<script>
import Alert from './components/common/Alert'
import NavigationBar from './components/common/NavigationBar'
import RegisterPopup from './components/login/RegisterPopup'
import LoginPopup from './components/login/LoginPopup'
import Footer from './components/common/Footer'
export default {
  name: 'App',
  components: {
    Alert,
    NavigationBar,
    RegisterPopup,
    LoginPopup,
    Footer
  },
  data() {
    return {
      navBarPosition: 'absolute',
      showLogin: false,
      showRegister: false,
      loginState: false,
      showAlert: false
    }
  },
  mounted() {
    // handleScroll is the callback function of the scroll event
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed(){
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    showMessage: function(message) {
      this.$message({
        message: message,
        offset: 0
      });
    },
    handleScroll() {
      // get scrolled distance
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      // check if the scrolled distance has exceeded the height of the header
      if (scrollTop != 0) {
        this.navBarPosition = 'fixed'
      } else {
        this.navBarPosition = 'absolute'
      }
    },
    changeTab: function (index) {
      if (index == 4) {
        this.$router.push('instruction')
        this.closeLoginPopup()
      } else if (this.$store.state.loginState) {
        switch (index) {
          case 1:
            this.$router.push('thinkcloud')
            break
          case 2:
            this.$router.push('trendi')
            break
          case 3:
            this.$router.push('solutions')
            break
        }
      } else {
        this.showMessage('请先登陆')
        this.showLoginPopup()
      }
    },
    showLoginPopup: function () {
      this.showLogin = true
      this.showRegister = false
    },
    closeLoginPopup: function () {
      this.showLogin = false
    },
    showRegisterPopup: function () {
      this.showRegister = true
      this.showLogin = false
    },
    closeRegisterPopup: function () {
      this.showRegister = false
    }
  }
}
</script>

<style lang="scss" scoped>
.message {
  width: 200px;
}
</style>
