<template>
  <div id="thinkcloudpage">
    <div class="side-column">
      <div class="side-column-title">
        欢迎使用兆瞳云思管理系统
      </div>
      <Collapse :collapse-data="composeList" :current-item="composeList.items[currentItem].key" @click-collapse-item="clickCollapseItem"/>
    </div>
    <div class="main-panel">
      <ComposeAd v-if="currentItem == 0" @page-change="pageChange"/>
      <ChooseDate v-if="currentItem == 1" @page-change="pageChange"/>
      <PayBill v-if="currentItem == 2" @page-change="pageChange"/>
    </div>
  </div>
</template>

<script>
import Collapse from '../elements/Collapse'
import ComposeAd from '../compose/ComposeAd'
import ChooseDate from '../compose/ChooseDate'
import PayBill from '../compose/PayBill'
export default {
  components: {
    Collapse,
    ComposeAd,
    ChooseDate,
    PayBill
  },
  data() {
    return {
      composeList: {
        title: '开始使用',
        items: [{
          key: 'upload',
          index: 0,
          name: '上传'
        },
        {
          key: 'choosedate',
          index: 1,
          name: '选择发布日期'
        },
        {
          key: 'paybill',
          index: 2,
          name: '发布'
        }]
      },
      currentItem: 0
    }
  },
  methods: {
    clickCollapseItem: function (item) {
      // console.log(this.composeList)
      this.currentItem = item.index
    },
    pageChange: function (operation) {
      if (operation == 'next') {
        this.currentItem += 1
      } else if (operation == 'previous') {
        this.currentItem -= 1
      }
    }
  }
}
</script>

