import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../components/views/HomePage'
import InstructionPage from '../components/views/InstructionPage'
import ThinkCloudPage from '../components/views/ThinkCloudPage'
import TrendiPage from '../components/views/TrendiPage'
import SolutionPage from '../components/views/SolutionPage'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: HomePage
  },
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/instruction',
    name: 'instruction',
    component: InstructionPage
  },
  {
    path: '/thinkcloud',
    name: 'thinkcloud',
    component: ThinkCloudPage
  },
  {
    path: '/trendi',
    name: 'trendi',
    component: TrendiPage
  },
  {
    path: '/solutions',
    name: 'solutions',
    component: SolutionPage
  }
]

const router = new VueRouter({
  routes: routes   // routers简写
})

router.beforeEach((to, from, next) => {   //全局全局前置守卫
  let ISLOGIN = router.app.$options.store.state.loginState
  if (to.name == 'home' || to.name == 'instruction') {
    next()
  } else {
    if (ISLOGIN) next()
    else next({name:'home'})
  }
})

export default router;