<template>
  <div class="swiper-container">
    <div class="swiper-item-container" 
      :class="{ 'no-transition': currentPic == 0 || currentPic == pseudoSwipeList.length - 1 }"
      :style="{ width: (pseudoSwipeList.length+1) * 100 + '%', transform: `translateX(${- 100 / pseudoSwipeList.length * currentPic + '%'})` }">
      <div 
        class="swiper-item"
        v-for="game in pseudoSwipeList" 
        :key="game.key">
        <div class="swiper-item-background" :style="{backgroundImage: `url(${game.src})`}" />
      </div>
    </div>
    <SwiperDots
      v-if="showSwiperDots"
      class="swiper-dot-box-container"
      :length="swipeList.length"
      :currentIndex="currentPic"
      @click-dot="swipe" />
  </div>
</template>

<script>
import SwiperDots from './SwiperDots'
export default {
  name: 'Swiper',
  components: {
    SwiperDots
  },
  props: {
    type: {
      type: String,
      default: 'auto'
    },
    swipeType: {
      type: String,
      default: 'interval'
    },
    time: {
      type: Number,
      default: 2000
    },
    showSwiperDots: {
      type: Boolean,
      default: true
    },
    swipeList: {
      type: Array
    },
    itemWidth: {
      type: String,
      default: '100'
    },
    unit: {
      type: String,
      default: 'vw'
    }
  },
  data() {
    return {
      currentPic: 1,
      pseudoSwipeList: [],
      interval: 0
    }
  },
  watch: {
    currentPic: function (currentVal) {
      this.$emit('swiper-changed', currentVal)
    }
  },
  created: function () {
    // add pseudo elements at the beginning & end of pseudoSwipeList to
    // create continuous swiping effect
    this.initializeSwiper()

    // start auto swiping if type === auto
    if (this.type === 'auto' && this.swipeType === 'interval') this.autoSwipe()
  },
  methods: {
    initializeSwiper: function () {
      // initialize params
      this.pseudoSwipeList = JSON.parse(JSON.stringify(this.swipeList))
      this.interval = this.time

      let pseudoElement = {
        key: 'pseudoFront',
        name: this.pseudoSwipeList[this.pseudoSwipeList.length - 1].name,
        src: this.pseudoSwipeList[this.pseudoSwipeList.length - 1].src
      }
      this.pseudoSwipeList.unshift(pseudoElement)
      pseudoElement = {
        key: 'pseudoBack',
        name: this.pseudoSwipeList[1].name,
        src: this.pseudoSwipeList[1].src
      }
      this.pseudoSwipeList.push(pseudoElement)
    },
    autoSwipe: function () {
      let _this = this
      let timer = setInterval(intervalCallback, this.interval);
      function intervalCallback () {
        if (_this.currentPic < _this.swipeList.length) {
          _this.interval = _this.time
          _this.currentPic++
        } else {
          _this.interval = 0
          _this.currentPic = 0
        }
        // Restart the timer every time to control its interval
        clearInterval(timer);
        timer = setInterval(intervalCallback, _this.interval);
      }
    },
    manualSwipe: function (e) {
      let swipeListLength = this.swipeList.length
      if (e === 'forward') {
        if (this.currentPic < swipeListLength) {
          this.currentPic++
        } else {
          this.currentPic = 0
          this.delay(1).then(() => { this.currentPic = 1 })
        }
      } else {
        if (this.currentPic > 1) {
          this.currentPic--
        } else {
          this.currentPic = swipeListLength + 1
          this.delay(1).then(() => { this.currentPic = swipeListLength })
        }
      }
    },
    delay: function (delayTime) {
      return new Promise((resolve) => {
        let timer = setTimeout(() => {
          clearTimeout(timer)
          resolve('success')
        }, delayTime)
      })
    },
    // Handle click dot event (index picker change event)
    swipe: function (index) {
      this.currentPic = index
    }
  }
}
</script>

<style lang="scss" scoped>
  .swiper-container {
    overflow: hidden;  
    text-align: center;
    .swiper-item-container {
      height: 100%;
      display: flex;
      transition: transform 700ms;
      .swiper-item {
        flex: 1;
        height: 100%;
        .swiper-item-background {
          width: 100%;
          height: 100%;
          background-position: center center;
          background-size: cover;
        }
      }
    }
    .no-transition {
      transition: none;
    }
    .swiper-dot-box-container {
      z-index: 3;
      position: relative;
      left: 50%;
      bottom: 50px;
      width: 300px;
      height: 30px;
      margin-bottom: 20px;
      transform: translateX(-50%);
      display: flex;
    }
  }
</style>