<template>
  <div class="navigation-bar-container">
    <div class="navigation-bar-logo-container" @click="goHomePage">
      <img class="navigation-bar-logo" src="../../assets/icons/logo-chinese.png" />
      <img class="navigation-bar-logo" src="../../assets/icons/logo.png" />
    </div>
    <div class="navigation-bar-tab-container">
      <div v-for="(tab, index) in tabs"
        class="navigation-bar-tab"
        :class="{ active: tab.selected }"
        :key="tab.key" 
        @click="changeTab(index)">
        {{tab.name}}
      </div>
    </div>
    <div v-if="!loginState" class="login-container">
      <span class="go-login" @click="$emit('show-login-popup')">
        登陆
      </span>
      <span class="go-register" @click="$emit('show-register-popup')">
        注册
      </span>
    </div>
    <div v-else class="login-container">
      <span class="go-profile" @click="$emit('go-profile')">
        账户详情
      </span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginState: this.$store.state.loginState,
      tabs: [
        {
          key: 'thinkCloud',
          name: '云思 Think Cloud',
          selected: false
        }, {
          key: 'trendi',
          name: '追浪 Trendi',
          selected: false
        }, {
          key: 'magaSolutions',
          name: '兆瞳 Solutions',
          selected: false
        }, {
          key: 'help',
          name: '帮助 Help',
          selected: false
        }
      ]
    }
  },
  watch:{
    '$store.state.loginState': function (newVal) {
      if (newVal) {
          this.loginState = this.$store.state.loginState
      }
    }
  },
  methods: {
    changeTab: function (index) {
      for (var i in this.tabs) {
        this.tabs[i].selected = false
      }
      this.tabs[index].selected = true
      this.$emit('change-tab', index + 1)
    },
    goHomePage: function () {
      this.$router.push('home')
    }
  }
}
</script>

