<template>
  <div class="pop-up">
    <img class="close-icon" src="../../assets/icons/closeIcon.png" @click="$emit('close-register')"/>
    <div class="pop-up-title">注册云思管理系统</div>
    <div class="input-container">
      <div class="input-title">手机号：</div>
      <el-input class="phone-num-input" v-model="phoneNum" placeholder="请输入手机号"></el-input>
    </div>
    <div class="input-container">
      <div class="input-title">设置密码：</div>
      <el-input class="phone-num-input" v-model="password" show-password></el-input>
    </div>
    <div class="input-container">
      <div class="input-title">确认密码：</div>
      <el-input class="phone-num-input" v-model="verifyPassword" show-password></el-input>
    </div>
    <div class="input-container">
      <div class="input-title">图形验证码：</div>
      <el-input class="verification-code-input" v-model="picCode"></el-input>
      <img class="pic-code" :src="picCodeUrl" @click="updatePicCode"/>
    </div>
    <div class="input-container">
      <div class="input-title">验证码：</div>
      <el-input class="verification-code-input" v-model="verificationCode"></el-input>
      <el-button class="verification-code-button" type="primary" @click="sendVerificationCode" :disabled="count != 60">{{verificationContent}}</el-button>
    </div>
    <div class="input-container">
      <el-button class="login-button" type="primary" @click="goRegister">注册</el-button>
    </div>
  </div>
</template>

<script>
const WEBAPI = require('apifm-webapi')
export default {
  data() {
    return {
      key: '',
      phoneNum: '',
      verificationCode: '',
      picCode: '',
      picCodeUrl: '',
      password: '',
      verifyPassword: '',
      verificationContent: '发送验证码',
      count: 60
    }
  },
  mounted: function() {
    this.updatePicCode()
  },
  methods: {
    updatePicCode: function() {
      let _key = Math.random()
      this.picCode = ''
      this.key = _key
      this.picCodeUrl = WEBAPI.graphValidateCodeUrl(_key)
    },
    sendVerificationCode: function () {
      let _this = this
      WEBAPI.smsValidateCode(this.phoneNum, this.key, this.picCode).then((res) => {
        if (res.code == 502) {
          _this.updatePicCode()
        } else {
          _this.count = 0
          _this.verificationContent = (60 - _this.count) + 's后重发'
          let interval = setInterval(() => {
            _this.count++
            _this.verificationContent = (60 - _this.count) + 's后重发'
            if (_this.count == 60) {
              _this.verificationContent = '发送验证码'
              clearInterval(interval)
            }
          }, 1000)
        }
      })
    },
    goRegister: function () {
      if (this.password == this.verifyPassword) {
        let data = {
          code: this.verificationCode,
          mobile: this.phoneNum,
          pwd: this.password,
          autoLogin: true
        }
        WEBAPI.register_mobile(data).then((res) => {
          if (res.code === 0) {
            this.$emit('close-register')
            this.$store.commit('SET_LOGIN_STATE', true)
            // this.$store.commit('token', res.data.token)
            // this.$store.commit('uid', res.data.uid)
          } else {
            this.$store.commit('SET_LOGIN_STATE', false)
          }
        })
      }
    }
  }
}
</script>

